<template>
  <div id='notice'>
    <div class='container-fluid' style="padding: 0px 0px 0px 0px">
      <Header :noticeIs='noticeIs' img="" school='Go语言实战抽奖系统'></Header>
    </div>
    <div class="col-md-10 col-md-offset-1 mt-6">
      <el-tabs v-model="activeName" type="border-card" class="mb-4">
        <el-tab-pane name="project">
          <div slot="label" style="font-size: 20px;margin-top: 20px"><i class="el-icon-tickets"></i> 消息列表</div>
          <div class="row" v-for="(item, key) in materialTypes" v-bind:key="key">
            <div class="col-md-12 col-xs-12 pull-left" style="margin-top: 10px">
              <div class="java_tit mb-4">
                <span>{{item.kind}}</span>
                <span class="pull-right" style="color: #000000;background: #ffffff;margin-top:1px">
                  {{item.time}}
<!--                  <i class="el-icon-caret-right"></i>-->
                </span>
              </div>
              <div class="row pl-4 pr-5">
                <span class="pull-left java_color" v-show="item.status">[{{item.status}}]</span><span class="ml-3">{{item.content}}</span><a class="pull-right font-small" @click="showStail(item)">查看详情</a>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import Header from '../../components/school-training/Header.vue'
// import Pager from '../../components/PageHelper/PageHelper.vue'
export default {
  name: 'notice',
  components: {
    Header
    // Pager
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      activeName: 'project',
      materialTypes: [],
      curPage: 1, // 当前页
      total: 10, // 总共页数
      pageSize: 20, // 每页记录数
      dataChanged: false,
      noticeIs: false
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      if (localStorage.getItem('unreadStatus') === 'false') {
        me.noticeIs = false
      } else {
        me.noticeIs = true
      }
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      this.loadProjectinfo()
    },
    loadProjectinfo () {
      let me = this
      me.materialTypes = []
      this.$http.post('/doproject/pushMessageInfo', {
      }, res => {
        console.log(res)
        if (res.success) {
          let k = 0
          for (let obj of res.data) {
            var status = '未读'
            if (obj.status === 1) {
              status = '已读'
              k++
            }
            var messageType = ''
            var url = ''
            if (obj.messageType === 1) {
              messageType = '提交成果物'
              url = 'task-list.html'
            } else if (obj.messageType === 2) {
              messageType = '点评成果物'
              url = 'task-list.html'
            } else if (obj.messageType === 3) {
              messageType = '系统点评'
              url = 'task-list.html'
            } else if (obj.messageType === 4) {
              messageType = '阶段报告'
              url = 'stage-report.html'
            }
            if (res.data.length === k) {
              localStorage.setItem('unreadStatus', 'false')
              me.noticeIs = false
            }
            me.materialTypes.push({
              id: obj.id,
              kind: messageType,
              time: obj.createTime,
              content: obj.message,
              url: url,
              status: status
            })
          }
          this.$http.post('/doproject/noticeInfo', {
          }, res => {
            console.log(res)
            if (res.success) {
              for (let obj of res.data) {
                me.materialTypes.push({
                  id: obj.id,
                  kind: '系统公告',
                  time: obj.createTime,
                  content: obj.notice,
                  url: obj.url,
                  status: ''
                })
              }
            }
          })
        }
      })
    },
    sizeChange (size) {
      return this.winWidth * size / 1920 + 'px'
    },
    refresh () { // 用于刷新组件，需手动调用
      this.dataChanged = true
      this.$nextTick(() => {
        this.dataChanged = false
      })
    },
    gotoPage (page) {

    },
    changeRowNum (pageSize) {

    },
    showStail (item) {
      if (item.kind !== '系统公告' && item.status === '未读') {
        this.$http.post('/doproject/updateMessageStatus', {
          'id': item.id
        }, res => {
          console.log(res)
          window.location = item.url
        })
      } else {
        window.location = item.url
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 210px;
    color: #fff;
    text-align: center;
    img{
      padding-top: 110px;
    }
  }
  .java_bg {
    background: #e66b46;
  }
  .java_color {
    color: #e66b46;
  }
  .pic_left {
    padding-bottom: 20px;
    padding-top: 10px;
    text-align: left;
    height: 90px;
    position: absolute;
    top: 0;
    left: 1rem;
    background: none;
    z-index: 10;
    padding-right: 0.4rem;
    h3{
      color:#ffffff;
      font-size: 25px
    }
    p{
      font-size: 16px;
      font-weight: 500;
    }
  }
  .java_tit {
    border-top: 2px solid #e66b46;
    span {
      padding: 10px 20px;
      display: inline-block;
      background: #e66b46;
      color: #ffffff;
    }
  }
  .line{
    float: left;
    width: 3px;
    height: 16px;
    background: #6612cf;
    overflow: hidden;
    margin-right: 5px;
  }
  .video-img{
    padding: 20px;
    width: 100%;
    border-radius: 25px;
  }
</style>
<style>
  .el-tabs--border-card > .el-tabs__header {
    background-color: #ffffff!important;
    border-bottom: none!important;
    margin: 0;
  }
  .el-tabs--border-card {
    background: #FFFFFF;
    border: none!important;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    border-radius: 5px;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border: none!important;
    margin-top: -1px;
    color: #909399;
  }
</style>
